import React, { Component, Fragment } from 'react';
import Portfolio from '../Portfolio';
import About from '../About';
import Contact from '../Contact';
import Skills from '../Skills';
import Infos from '../Infos';

import { PROFILE, ABOUT, DETAILS, SKILLS, BLOG, PORTIFOLIO } from '../../services/api'

class ListSection extends Component {  
  constructor(props){
    super(props)
    this.state = {
      profile: [], 
      about: [], 
      skills: [], 
      details: [],
      ready: false
     }
  }
  async componentDidMount(){
    const profile = await PROFILE();
    const about = await ABOUT();
    const details = await DETAILS();
    const skills = await SKILLS();
    const portfolio = await PORTIFOLIO();

    this.setState({ profile, about, skills, details, portfolio, ready: true });
  }
  render(){
    const { portfolio, profile, about, skills, details, ready } = this.state;

    if(ready){
      return (
        <Fragment>
          <Portfolio portfolio={portfolio} />
          <About about={about} curriculum={profile.curriculum} />
          <Skills skills={skills} />
          <Infos details={details} />
          <Contact />
        </Fragment>
      );
    }else{
      return null;
    }
  }
}
export default ListSection;